@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap');

html, body{
  box-sizing: border-box;
  margin: 0;
  padding: 0;

  user-select: none;
}

body{
  position: relative;
  background-color: aliceblue;
  font-family: 'Ubuntu', sans-serif;
}

h1,h2, h3, h4, h5{
  margin: 0;
  font-weight: normal;
}

h1, h3{
  padding: 10px;
  font-weight: 300;
}

h1{
  font-size: 2.5rem;
}

header{
  width: 100%;
  height: 400px;
  margin: 0;
  padding: 0;
  background-color: rgb(44, 46, 49);

  color: white;
  text-align: center;
}

.title{
  padding: 50px;
}

.card{
  position: relative;
  display:block;
  width: 600px;
  min-height: 400px;
  margin: -170px auto;
  background-color: white;
  box-shadow: 10px 10px 10px rgb(0 0 0 / 0.3);
}

.card-header{
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 120px;
  margin: 0 auto;
  padding: 20px;
  background-color: azure;
  border-bottom: white 1px solid;
  text-align: center;
  pointer-events: none;
}

.card-header img{
  position: absolute;
  top: 20%;
  left: 40%;
  padding: 5px;
  box-shadow: 0 0 0 10px inset white;
  border: white 1px solid;
  border-radius: 50%;
  transform: translateY(-50%, 10%);
}

.card-body{
  box-sizing: border-box;
  width: 100%;
  min-height: 70px;
  margin: 90px auto 0;
  text-align: center;
}

.card-title{
  margin: 15px;
}

.card-title h5{
  font-size: 1.6rem;
  font-weight: 400;
}


.card-text{
  margin: 10px;
  font-size: 1rem;
  color: rgb(180, 180, 180);
}

.card-footer{
  box-sizing: border-box;
  width: 100%;
  margin: 10px 0 0;
  padding: 10px;
}

ul{
  display: flex;
  justify-content: space-around;
  box-sizing: border-box;
  margin: 0 50px 20px;
  padding: 0;
  overflow: hidden;
  
}

li{
  display: block;
  width: 50px;
  height: 65px;
  overflow: hidden;
  list-style: none;
  cursor: pointer;
}

li img{
  transform: translateY(15px);
  transition: all 400ms ease;
}

.active{
  transform: translateY(-65px);
}

#userimg{
  background-image: url(./imgs/user.svg);
  min-width: 128px;
  min-height: 124px;
  background-repeat: no-repeat;
}

#emailimg{
  background-image: url(./imgs/email.svg);
  min-width: 128px;
  min-height: 124px;
  background-repeat: no-repeat;
}

#birtimg{
  background-image: url(./imgs/birthday.svg);
  min-width: 128px;
  min-height: 124px;
  background-repeat: no-repeat;
}

#addressimg{
  background-image: url(./imgs/address.svg);
  min-width: 128px;
  min-height: 124px;
  background-repeat: no-repeat;
}

#phoneimg{
  background-image: url(./imgs/phone.svg);
  min-width: 128px;
  min-height: 124px;
