}

#nickimg{
  background-image: url(./imgs/password.svg);
  min-width: 128px;
  min-height: 124px;
  background-repeat: no-repeat;
}

.arrows{
  position: absolute;
  top: 45%;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  width: 100%;
  padding: 0 25px;
}

.arrow-left, .arrow-right{
  position: absolute;
  top: 45%;
  padding: 0 25px;
  font-size: 2.5rem;
  color: #cccccc;
  cursor: pointer;
  opacity: 1;
  
}

.arrow-left{
  left: 0;
 
}
.arrow-right{
  right: 0;
}

.arrow-left:hover{
  transform: translateX(-3px);
  color: green;
}
.arrow-right:hover{
  transform: translateX(3px);
  color: green;
}